<template>
<div id="calc">
  <p v-if="errors.length">
    <b>Please correct the following error(s):</b>
    <ul>
      <li id="error" v-for="error in errors" :key="error.indexOf()">{{ error }}</li>
    </ul>
  </p>
  <form v-on:submit.prevent="formValidator">
    <input type="text" v-model="step.ssr_prob" placeholder="% SSR" />
    <input type="text" v-model="step.ssr_rate" placeholder="Rate Up %" />
    <input type="checkbox" v-model="isSSR" />
    <input type="text" v-model="step.ssr_prob_guaranteed" :disabled="!isSSR" placeholder="% SSR" />
    <input type="text" v-model="step.ssr_rate_guaranteed" :disabled="!isSSR" placeholder="Rate Up %" />
    <button type="submit">Add</button>
  </form>
  <div>
    <ul>
      <li v-for="(step, index) in steps" :key="step.id">
        <span v-if="step.ssr_prob_guaranteed && step.ssr_rate_guaranteed">
          {{ index + ' -  X ‚Üù H ('
          + step.ssr_rate + ' ; '
          + step.ssr_prob + ' ; '
          + step.ssr_prob_guaranteed + ' ; '
          + step.ssr_rate_guaranteed +
          ') '}}
        </span>
        <span v-else>
          {{ index + ' -  X ‚Üù H ('
          + step.ssr_rate + ' ; '
          + step.ssr_prob +
          ') '}}
        </span>
        <button id="remove" v-on:click="removeRow(index)"> Remove </button>
      </li>
    </ul>
  </div>
</div>
</template>

<script>
export default {
  name: 'Calc',
  components: {},
  methods: {

    formValidator() {
      this.errors = [];

      if (!this.step.ssr_prob) {
        this.errors.push('SSR probability required.');
      }
      if (!this.step.ssr_rate) {
        this.errors.push('SSR rate up required.');
      }
      if (this.isSSR && !this.step.ssr_prob_guaranteed) {
        this.errors.push('SSR guaranteed probability required.');
      }
      if (this.isSSR && !this.step.ssr_rate_guaranteed) {
        this.errors.push('SSR guaranteed rate up required.');
      }
      if (this.errors.length === 0) {
        this.addRow()
      }
    },

    addRow() {
      let newStep = {
        ssr_prob: this.step.ssr_prob,
        ssr_rate: this.step.ssr_rate
      }
      if (this.isSSR) {
        newStep = Object.assign(newStep, {
          ssr_prob_guaranteed: this.step.ssr_prob_guaranteed,
          ssr_rate_guaranteed: this.step.ssr_rate_guaranteed
        })
      }
      this.steps.push(newStep);
    },

    removeRow(indexToRemove) {
      this.steps = this.steps.filter((_, index) => index !== indexToRemove)
    }
  },

  data() {
    return {
      errors: [],
      steps: [{
          ssr_prob: 1,
          ssr_rate: 2,
          ssr_prob_guaranteed: 3,
          ssr_rate_guaranteed: 4,
        },
        {
          ssr_prob: 5,
          ssr_rate: 6,
          ssr_prob_guaranteed: 7,
          ssr_rate_guaranteed: 8,
        }
      ],
      step: {
        ssr_prob: undefined,
        ssr_rate: undefined,
        ssr_prob_guaranteed: undefined,
        ssr_rate_guaranteed: undefined,
      },
      isSSR: false
    }
  }
}
</script>

<style>
input:not(:enabled) {
  visibility: hidden;
}

#calc input[type=text],
select {
  width: 17%;
  padding: 10px 20px;
  margin: 10px 0 0 10px;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

#calc input[type=checkbox] {
  padding: 10px 20px;
  margin: 10px 0 0 10px;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
}

#calc button[type=submit] {
  width: 10%;
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  margin: 10px 0 0 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

#calc button[type=submit]:hover {
  background-color: #45a049;
}

#remove {
  width: 15%;
  background-color: #e02727;
  color: white;
  padding: 10px 20px;
  margin: 10px 0 0 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

#remove:hover {
  background-color: #9e1c1c;
}

#error {
  color: red;
}

#calc {
  border-radius: 5px;
  background-color: rgba(160, 80, 200, 0.20);
  padding: 20px;
}
</style>
